
$primary-color: #1f1f1f;

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: Arial, Helvetica, sans-serif;
	background: $primary-color;
	color: #fff;
	line-height: 1.5;
}

ul {
	list-style: none;
}

.container {
	max-width: 1100px;
	margin: 0 auto;
	padding: 0 20px;
	overflow: auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	height: 100vh;
}

header {
	text-align: center;
	margin-bottom: 40px;
	background: rgba(0, 0, 0, 0.3);
	padding: 30px;
	display: flex;
	width: 100vw;

}

#root{
	width: 100vw;
	height: 100vh;
	
}



.Galaxy{
	min-height: 100%;
	min-width: 100%;

}

.Row1{
	div:nth-child(1){
		border: 2px white solid;
		min-height: 100vh;
		min-width: 100vw;
	}
	div:nth-child(2){
		border: 2px white solid;
		min-height: 100vh;
		min-width: 100vw;
	}
	div:nth-child(3){
		border: 2px white solid;
		min-height: 100vh;
		min-width: 100vw;
	}
}
.Row2{
	div:nth-child(1){
	
		min-height: 100vh;
		min-width: 100vw;
		position: relative;
		img{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(50%,-50%);
			width: 15%;
		}
	}
	div:nth-child(2){
	
		min-height: 100vh;
		min-width: 100vw;
		position: relative;
		img{
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%,-50%);
			width: calc(100% - 60%);
			@media (min-width:1px) and (max-width:425px) {
				width: calc(100% - 10%);
			}
		}
		p{
			font-size: 2vw;
			padding: 2vw;
			@media (min-width:1px) and (max-width:425px) {
				font-size: 4vw;
			padding: 10vw;
			}
			@media (min-width:426px) and (max-width:768px) {
				font-size: 3vw;
			padding: 6vw;
			}
		}
	}
	div:nth-child(3){
		
		min-height: 100vh;
		min-width: 100vw;
	}
}

.Row3{
	div:nth-child(1){
		border: 2px white solid;
		min-height: 100vh;
		min-width: 100vw;
	}
	div:nth-child(2){
		border: 2px white solid;
		min-height: 100vh;
		min-width: 100vw;
	
	}
	div:nth-child(3){
		border: 2px white solid;
		min-height: 100vh;
		min-width: 100vw;
	}
}
.flex{
	display: flex;
	.spaced{
		justify-content: space-between;
	}
}
.Unset{
	min-height: unset!important;
}
.bottom{
	position: absolute;
    bottom: 0;
    width: 100%;
}

.left{
	position: absolute;
    top: 41%;
    z-index: 2;
    left: -26px;
}

.right{
	position: absolute;
    top: 41%;
    z-index: 2;
    right: -26px;
}